<template>
  <div>

  <div @click="getCity">get</div>
  <mt-index-list >
    <mt-index-section index="A">
      <mt-cell v-for="option in options" :title="option.CN"></mt-cell>
    </mt-index-section>
    <mt-index-section index="B">
      <mt-cell title="Baldwin"></mt-cell>
      <mt-cell title="Braden"></mt-cell>
    </mt-index-section>
    ...
    <mt-index-section index="Z">
      <mt-cell title="Zack"></mt-cell>
      <mt-cell title="Zane"></mt-cell>
    </mt-index-section>
  </mt-index-list>

  </div>
</template>

<script>
    export default {
        name: "city-l-ist",
      data () {
        return {
          options: [],
          sites: []
        }
      },
      created () {
        this.getCity();
      },
      methods: {
          getCity: function(){
            var url = "http://127.0.0.1:8081/";
            let   dataTEXT='{"request":{"authKey":"","deviceID":"1111","platform":"H5","version":"1.0","location":"(0,0)","sessionID":"","channel":"2001","userID":"","userName":"hgwxt","password":"a123451","aesPwd":"a123451","requestType":"LoginWithMD5Pwd"}}'
            this.$http.post(url+"City", {dataJson: dataTEXT}, {emulateJSON: true}).then(function (res) {
              console.log(res);

              this.options=res.body.d.cityCodeList.sort(function(a,b){
                if(a.PY>b.PY){
                  return 1
                }else{
                  return -1
                }
              });
              let citylist={"indexval":"A","list":[]};
              let list=[];
              let self=this;
              for(let a in self.options){
                if(self.options[a].PY.substr(0,1).indexOf(citylist.indexval)==-1){
                  list.push(citylist);
                  citylist.list=[];
                  citylist.indexval="";
                  citylist.indexval=self.options[a].PY.substr(0,1);
                  console.log(self.options[a].PY.substr(0,1))
                  citylist.list.push(self.options[a]);

                }else{
                  citylist.list.push(self.options[a]);
                  console.log(1)
                }
              }
              console.log(list)
            }, function (err) {
              console.log(err)
              alert(err)
            });
          },
      },
      components: {

      }
    }
</script>

<style scoped>

</style>



