<template>
  <div class="login-view">
    <h1 >
      <a href="javascript:history.go(-1);" v-show="isShowBack">取消</a>登录
    </h1>
    <form method="get" @submit.prevent="onSubmit()">
      <p v-if="error" class="tip error">{{error}}</p>
      <div class="form-user">
        <label>
          <strong>邮箱</strong>
          <input
            v-model="email"
            type="email"
            name="email"
            @input="updateData"
            placeholder="邮箱">
        </label>
      </div>
      <div class="form-pwd">
        <label>
          <strong>请输入密码</strong>
          <template v-if="passType === 'password'">
            <input
              v-model="token"
              type="password"
              name="token"
              @input="updateData"
              placeholder="Token">
          </template>
          <template v-if="passType === 'text'">
            <input
              v-model="token"
              type="text"
              name="token"
              @input="updateData"
              placeholder="Token">
          </template>
          <span class="show-pwd" :class="{show: isShow}" @click="showPwd()"></span>
        </label>
      </div>
      <!-- <div class="form-element hide">
        <input id="remember" type="checkbox" name="remember" checked="">
        <label for="remember">下次自动登录</label>
      </div> -->
      <div class="">
        <button
          class="submit"
          type="submit"
          :disabled="isDisabled"
          :class="{disabled: isDisabled}">
          {{loginState}}
        </button>
      </div>
    </form>
    <div class="footer">
      <div class="more-login">使用其他方式登录 &amp; 找回密码</div>
      <div class="btns">
        <router-link :to="{name: 'Home'}">注册帐号</router-link>
        <a href="#">下载</a>
      </div>
    </div>
  </div>
</template>

<script>
  // import Headers from './Header.vue';
  import { Toast } from 'mint-ui'
  import { Indicator } from 'mint-ui';
  import User from '../router/user';
  export default {
    name: 'login',
    data () {
      return {
        email:"",
        token:"",
        loginState: '登录',
        isDisabled: false,    // Disabled submit button
        isShow: 0,            // Show pwd
        passType: 'password',
        error: '',             // Verification results
        isLoginSun:false
      }
    },
    props:[
      'isShowBack',
    ],
    created(){
    },
    methods:{
      login:function(){
        var url = User.httpInfo.httpValueText;
        Indicator.open({
          text: 'Loading...',
          spinnerType: 'fading-circle'
        });
        var url2 = "http://m.shinetour.com:8080/ShineTourMobileWebMiddleService/service.php";
//post请求有三个参数,第一个是接口url,第二是传递的数据,第三个是一个固定的写法{emulateJSON:true}
// ,then中有两个参数,第一个是成功向服务器发送请求时调用,第二个没有连接到服务器时调用
        let   dataTEXT='{"request":{"authKey":"","deviceID":"1111","platform":"H5","version":"1.0","location":"(0,0)","sessionID":"","channel":"2001","userID":"","userName":"hgwxt","password":"a123451","aesPwd":"a123451","requestType":"LoginWithMD5Pwd"}}'
        this.$http.post(url+"LoginWithMD5Pwd", {dataJson: dataTEXT}, {emulateJSON: true}).then(function (res) {
          console.log(res);
          Indicator.close();
          this.onSuccess(res)
        }, function (err) {
          console.log(err)
          Indicator.close();
          this.onError(err)
        });
      },
      getData: function () {


      },

      showPwd: function () {
        this.isShow = this.isShow ? 0 : 1
        this.isShow ? this.passType = 'text' : this.passType = 'password'
      },
      updateData: function (e) {
        // v-model Form handling
        // this.$store.commit({
        //   type: 'updateData',
        //   name: e.target.name,
        //   value: e.target.value
        // })
      },
      beforeSubmit: function () {
        // console.log('Submiting...')
        this.isDisabled = true
        this.loginState = '正在登录...'
      },
      onSuccess: function (res) {
        this.isLoginSun=true;
        User.state.login_name=res.body.d.userInfo;
        this.$router.push({name: 'Home'})
      },
      onError: function (err) {
        this.error = err.body.error
        this.loginState = '登录'
        this.isDisabled = false
        Toast({
          message: '登陆失败请稍后再试...',
          position: 'middle',
          duration: 2000
        });
      },
      onSubmit: function () {
        if(this.checkInfo()){
          this.beforeSubmit()
          this.login();
        }else{

        }

      },
      checkInfo:function(){
        if(this.email==""||this.token==""){
          Toast({
            message: '请输入账号或密码',
            position: 'bottom',
            duration: 2000
          });
          return false;
        }else{
          return true;
        }

      }
    },

  components:{
    // Headers
  }
  }
</script>

<style lang="scss" scoped>
  .login-view {
    h1 {
      height: 4.5rem;
      margin: 0 0 1rem 0;
      padding: 0 1.8rem;
      line-height: 4.5rem;
      text-align: center;
      font-size: 1.8rem;
      font-weight: bold;

      a {
        position: absolute;
        left: 1.8rem;
        top: 0;
        color: #42bd56;
        font-size: 1.5rem;
        font-weight: normal;
      }
    }

    form {
      padding: 2rem 1.5rem;

      strong {
        font-size: 1.5rem;
        color: #222;
        display: none;
        margin-bottom: 0.5rem;
      }

      input[type="email"], input[type="text"], input[type="password"] {
        display: inline-block;
        width: 100%;
        height: 4.4rem;
        padding: 0 0.8rem;
        box-sizing: border-box;
        font-size: 1.5rem;
        background: #fff;
        border: 0.1rem solid #ccc;
        border-top-left-radius: 0.3rem;
        border-top-right-radius: 0.3rem;
        outline: 0;
      }

      .form-pwd {
        position: relative;

        input {
          padding-right: 4rem;
          border-top: 0;
        }

        .show-pwd {
          position: absolute;
          right: 0.2rem;
          top: 0;
          display: block;
          height: 100%;
          width: 3.2rem;
          background-image: url(data:image/png;base64,);
          background-repeat: no-repeat;
          background-position: center center;
          background-size: 2.2rem;
          z-index: 3;
        }

        .show-pwd.show {
          background-image: url(data:image/png;base64,);
        }
      }

      .submit {
        cursor: pointer;
        width: 100%;
        padding: 1.2rem 2.6rem;
        margin-top: 1rem;
        font-size: 1.7rem;
        text-align: center;
        color: #fff;
        background: #17AA52;
        border: 0.1rem solid #17AA52;
        border-radius: 0.3rem;
      }

      .disabled {
        cursor: not-allowed;
        background: #eee;
        border: none;
      }

      .tip {
        font-size: 1.4rem;
        color: #aaa;
      }

      .error {
        color: #ff0000;
      }
    }

    .footer {
      .more-login {
        font-size: 1.5rem;
        color: #aaa;
        text-align: center;
      }

      .btns {
        margin-top: 4rem;
        text-align: center;
        font-size: 1.5rem;

        a {
          color: #42bd56;
          margin-right: 1.5rem;
        }
      }
    }
  }
</style>



